import classes from "./Note.module.css";

function Note({title : titulo = "sem titulo", text = "texto default"}) {
  return (
    <div className={classes.noteCard}>
      <h3 className={classes.noteTitle}>
        {titulo}
      </h3>
      <p className={classes.noteText}>
        {text}
      </p>
    </div>
  );
}

export default Note;